<template>
  <li @click="getSongSheetList(item.id)">
    <div class="card-thumb">
      <img v-lazy="item.picUrl" alt class="auto-img" />
      <span>{{translatePlayCount(item.playCount)}}</span>
    </div>
    <p class="desc">{{item.name}}</p>
  </li>
</template>

<script>
export default {
  props: {
    item: Object,
  },
  methods: {
    translatePlayCount(n) {
      if (n > 99999999) {
        n / 10000;
        return Number(n / 100000000).toFixed(2) + "亿";
      }
      if (n > 9999) {
        n / 10000;
        return Number(n / 10000).toFixed(1) + "万";
      }
    },
    getSongSheetList(id){
      this.$emit("goSongSheetList",id)
    }
  },
};
</script>

