<template>
  <div class="recommend">
    <HomeTitle>推荐歌单</HomeTitle>
    <ul class="recommend-card">
      <Recommend-card
        v-for="(item,index) in recommendSongSheet"
        :key="index"
        :item="item"
        @goSongSheetList="toSongSheetList($event)"
      ></Recommend-card>
    </ul>
    <HomeTitle>最新音乐</HomeTitle>
    <ul class="recommend-new">
      <Recommend-new
        v-for="(item,index) in newMusic"
        :key="index"
        :newItem="item"
        @translate-currentid="$emit('translate-currentid',$event)"
      ></Recommend-new>
    </ul>
  </div>
</template>

<script>
import "../assets/less/recommend.less";
import HomeTitle from "../components/Hometitle";
import RecommendCard from "../components/RecommendCard";
import RecommendNew from "../components/RecommendNew";

export default {
  name: "Recommend",
  components: {
    HomeTitle,
    RecommendCard,
    RecommendNew,
  },
  data() {
    return {
      recommendSongSheet: [],
      newMusic: [],
    };
  },
  created() {
    this.getRecommendSongSheet();
    this.getnewMusic();
  },
  methods: {
    //获取推荐歌单
    getRecommendSongSheet() {
      this.recommendSongSheet = [];

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "http://musicapi.leanapp.cn/personalized?limit=6",
      })
        .then((result) => {
          this.$toast.clear();
          //
          if (result.data.code == 200) {
            this.recommendSongSheet = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //获取最新音乐
    getnewMusic() {
      this.newMusic = [];

      this.axios({
        method: "GET",
        url: "http://musicapi.leanapp.cn/personalized/newsong",
      })
        .then((result) => {
          //

          if (result.data.code == 200) {
            this.newMusic = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    toSongSheetList(id) {
      this.$router.push({ name: "Songsheetlist", query: { id: id } });
    },
  },
};
</script>

<style lang="less" scoped>
</style>